'use strict';

module.exports = function (string) {
  if (typeof string !== 'string') {
    throw new TypeError('Expected a string');
  }

  string = string.trim();

  if (string.includes(' ')) {
    return false;
  }

  try {
    new URL(string); // eslint-disable-line no-new

    return true;
  } catch (_unused) {
    return false;
  }
};